
body{
font-family: sans-serif;
width: 100vw;
min-height: 100vh;
}

.content-wrap{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: stretch;
      align-items: stretch;
}
.header-wrap{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: stretch;
      align-items: stretch;
}
.header-center,
.header-side{
  -webkit-box-shadow: 1px 10px 15px rgba(0,20,20, .5);
          box-shadow: 1px 10px 15px rgba(0,20,20, .5)
}
.header-center{
  background:rgba(0,120,150, 1);
  margin: 0 10px;
  /* border-left: solid white 10px;
  border-right: solid white 10px; */
  padding: 1rem;
}
.header-side{
  background: rgba(0,0,0,.9);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: stretch;
      justify-content: stretch;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-positive: 100;
      flex-grow: 100;
}
.score{
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-positive: 100;
      flex-grow: 100;
}
.high-score{
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-positive: 100;
      flex-grow: 100;
}

.content-wrap h2{
  opacity: 1;
  text-align: center;
  text-transform: uppercase;
  font-weight: 400;
  letter-spacing: .1rem;
  max-width: 80rem;
  padding: 2rem 2.5rem;
  background: white;
  -webkit-box-shadow: 1px 5px 25px rgba(0,20,20, .1);
          box-shadow: 1px 5px 25px rgba(0,20,20, .1);
  margin: 3rem auto;

}
input,
button{
  height: 1.4rem;
  padding: .3rem .8rem;
  border: none;
  font-size: 1rem;
  line-height: 1rem;
  margin: 0 1rem;
  -webkit-box-sizing: unset;
          box-sizing: unset;
  border: solid 2px rgba(0,0,0, 1);
  border-radius: 5px;
  text-align: center;
}

button{
  text-transform: uppercase;
  background: rgba(0,0,0,.9);
  border: solid 2px rgba(0,0,0,1);
  color: white;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  cursor: pointer

}
button:hover{
  border: solid 2px rgba(255,255,255,1);
}

.header-wrap p{
  color: white;
  text-transform: uppercase;
  font-size: 1rem;
  margin: 0 1rem;
  display: -ms-flexbox;
  display: flex;
  min-width: 15rem;
}
.header-wrap span{
  border-left: solid white 1px;
  border-right: solid white 1px;
  margin: 0 1rem;
  min-width: 3rem;
  text-align: center;
}


.gameWrap{
  -webkit-box-shadow: 1px 10px 15px rgba(0,20,20, .5);
          box-shadow: 1px 10px 15px rgba(0,20,20, .5);
  margin-bottom: 2rem;

}

.card{
  width: 13vw;
  height: 13vw;
  background: rgba(0,0,0,.9);
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  overflow: hidden;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  border: solid 2px rgba(0,0,0,.5);
}
.card:hover{
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  border: solid 3px white;

}

.card img{
  -o-object-fit: cover;
     object-fit: cover;
}
.gameWrap{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-line-pack: justify;
      align-content: space-between;
  width: 62vw;
  height: 45vw;
  padding: 2vw;
  margin: auto;
  margin-top: 2vw;
  background: rgba(0,0,0,.9);
  
}



